<template>
  <div class="header">
    <div class="header-left">
      <router-link tag="div" to="/" class="header-logo">
        <h4 class="logo-title">DECA.FINANCE</h4>
      </router-link>
    </div>
    <BaseMenu :show="true" />
  </div>
</template>

<script>
import BaseMenu from '@/components/Base/BaseMenu.vue'
import BaseLanguage from '@/components/Base/BaseLanguage.vue'
export default {
  name: 'BaseHeader1',
  components: {
    BaseMenu,
    BaseLanguage,
  },
  props: {
    msg: String,
    show: false,
  },
  data() {
    return {
      scrollTop: 100,
    }
  },
  computed: {
    bg() {
      if (this.show) {
        return 'background: #0D3366'
      }
      if (this.scrollTop > 435) {
        return 'background: #0D3366'
      }
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll(e) {
      this.scrollTop =
        e.target.documentElement.scrollTop || e.target.body.scrollTop
      console.log(this.scrollTop)
    },
    switchLang(lang) {
      console.log(lang)
      localStorage.setItem('lang', lang)
      this.$i18n.locale = lang
    },
  },
}
</script>

